@font-face {
  font-family: "GoodTimes";
  src: url("font/good_times.OTF");
  font-weight: 800;
  font-style: normal;
}
.tcs .leaderboard_table {
  position: absolute;
  width: 100%;
  height: fit-content;
  top: 226px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  color: #002a4d;
}
.tcs .leaderboard_container {
  width: 1920px;
  height: 1080px;
  background-color: transparent;
  background-image: none;
}
.tcs .row_container {
  display: flex;
  gap: 10px;
}
.tcs .row_container .rank_container {
  height: 75px;
  width: 117px;
  margin-left: 5px;
  background-color: white;
  border-bottom: 5px solid #ffe400;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  font-family: "GoodTimes";
}
.tcs .row_container .name_container {
  height: 75px;
  width: 1107px;
  background-color: white;
  border-radius: 0;
  border-bottom: 5px solid #ffe400;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  font-family: "GoodTimes";
  font-size: 26px;
}
.tcs .row_container .info_box {
  height: 75px;
  width: 116px;
  border-radius: 0;
  background-color: white;
  border-bottom: 5px solid #ffe400;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "GoodTimes";
  font-size: 26px;
  color: #002a4d;
}
.tcs .header_container {
  display: flex;
  gap: 10px;
  box-shadow: none;
  border-radius: 0;
}
.tcs .header_container .rank_header {
  width: 117px;
  height: 40px;
  box-shadow: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 36px;
  color: white;
  font-family: "GoodTimes";
  background-color: #0070be;
}
.tcs .header_container .name_header {
  width: 1107px;
  height: 40px;
  box-shadow: none;
  border-radius: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 36px;
  color: white;
  font-family: "GoodTimes";
  background-color: #0070be;
}
.tcs .header_container .info_header {
  width: 116px;
  height: 40px;
  box-shadow: none;
  border-radius: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: "GoodTimes";
  background-color: #0070be;
}

